import { Zoneless } from "./interfaces";
import { PlatformDetails, isMobile } from "./dependencies";

const LOGTAG = "windowstateobserver";

/**
 * Types of window states that are observed by the WindowStateObserver
 */
export const enum WindowStateType {
    FOCUS,
    MOUSE_FOCUS,
    FULLSCREEN
}

/**
 * This class is a centralized source for window states (e.g. focus, video states, visibility).
 * Other classes in the project should use this class to receive updates about the state and query the current value
 * and should not directly listen for or query them themselves.
 */
export class WindowStateObserver {
    private windowStateListeners: Map<WindowStateType, Function[]> = new Map();

    private focusFunc: Function;
    private blurFunc: Function;
    private fullscreenFunc: Function;
    private mouseEnterFunc: Function;
    private mouseLeaveFunc: Function;

    private windowFocus: boolean;
    private fullscreen: boolean = false;
    private mouseFocus: boolean;

    private isMobile: boolean;

    public constructor(platformDetails: PlatformDetails) {
        this.isMobile = isMobile(platformDetails);
        // Assume initial mouse focus is the same as window focus, even though this may not be correct
        // There are no dedicated APIs to get the current mouse focus at any point in time
        // Indirect methods, such as mouse events or HTML hover states, have edge cases that they may miss
        // In most scenarios, the desync will be closed when the user first interacts with the stream
        this.windowFocus = this.mouseFocus = document.hasFocus();

        const zoneless = (window as any).zoneless as Zoneless;
        const windowAddEventListener =
            zoneless?.windowAddEventListener?.bind(window) ?? window.addEventListener.bind(window);
        const documentAddEventListener =
            zoneless?.documentAddEventListener?.bind(document) ??
            document.addEventListener.bind(document);

        this.focusFunc = this.onFocus.bind(this);
        this.blurFunc = this.onBlur.bind(this);
        this.fullscreenFunc = this.onFullscreenChange.bind(this);
        this.mouseEnterFunc = this.onMouseEnter.bind(this);
        this.mouseLeaveFunc = this.onMouseLeave.bind(this);

        windowAddEventListener("focus", this.focusFunc);
        windowAddEventListener("blur", this.blurFunc);
        [
            "fullscreenchange",
            "webkitfullscreenchange",
            "mozfullscreenchange",
            "msfullscreenchange"
        ].forEach(eventType => documentAddEventListener(eventType, this.fullscreenFunc));
        documentAddEventListener("mouseenter", this.mouseEnterFunc);
        documentAddEventListener("mouseleave", this.mouseLeaveFunc);

        this.windowStateListeners.set(WindowStateType.FOCUS, []);
        this.windowStateListeners.set(WindowStateType.FULLSCREEN, []);
        this.windowStateListeners.set(WindowStateType.MOUSE_FOCUS, []);
        // update with current fullscreen state
        this.onFullscreenChange();
    }

    /**
     * Uninitialize the window state observer
     * @note Must be called before class leaves scope
     */
    public uninitialize() {
        const zoneless = (window as any).zoneless as Zoneless;
        const windowRemoveEventListener =
            zoneless?.windowRemoveEventListener?.bind(window) ??
            window.removeEventListener.bind(window);
        const documentRemoveEventListener =
            zoneless?.documentRemoveEventListener?.bind(document) ??
            document.removeEventListener.bind(document);

        windowRemoveEventListener("focus", this.focusFunc);
        windowRemoveEventListener("blur", this.blurFunc);
        [
            "fullscreenchange",
            "webkitfullscreenchange",
            "mozfullscreenchange",
            "msfullscreenchange"
        ].forEach(eventType => documentRemoveEventListener(eventType, this.fullscreenFunc));
        documentRemoveEventListener("mouseenter", this.mouseEnterFunc);
        documentRemoveEventListener("mouseleave", this.mouseLeaveFunc);

        for (const listeners of this.windowStateListeners.values()) {
            listeners.splice(0);
        }
    }

    /**
     * Get whether or not the application window has focus
     * @returns Whether the application window has focus
     */
    public getWindowFocus(): boolean {
        return this.windowFocus;
    }

    /**
     * Set the window focus state
     * @param focus Value to set the window focus state to
     * @note Should only be used in rare cases, where the application is aware of the state before the browser fires the corresponding events.
     */
    public setWindowFocus(focus: boolean) {
        // On mobile devices, the default use case does not have a pointing device connected at all times
        // Instead of only relying on mouseenter/mouseleave, use window focus as a fallback
        if (this.isMobile) {
            this.setMouseFocus(focus);
        }
        if (this.windowFocus !== focus) {
            this.windowFocus = focus;
            for (const listener of this.windowStateListeners.get(WindowStateType.FOCUS)!) {
                listener(focus);
            }
        }
    }

    /**
     * Get whether or not the application window is in fullscreen
     * @returns Whether the application window is in fullscreen
     */
    public getFullscreen(): boolean {
        return this.fullscreen;
    }

    private setFullscreen(fullscreen: boolean) {
        if (this.fullscreen !== fullscreen) {
            this.fullscreen = fullscreen;
            for (const listener of this.windowStateListeners.get(WindowStateType.FULLSCREEN)!) {
                listener(fullscreen);
            }
        }
    }
    /**
     * Get whether or not the application window has mouse focus
     * @returns Whether the application window has mouse focus
     */
    public getMouseFocus(): boolean {
        return this.mouseFocus;
    }

    private setMouseFocus(focus: boolean) {
        if (this.mouseFocus !== focus) {
            this.mouseFocus = focus;
            for (const listener of this.windowStateListeners.get(WindowStateType.MOUSE_FOCUS)!) {
                listener(focus);
            }
        }
    }

    /**
     * Register a listener for a window state
     * @param type Type of window state to listen for
     * @param listener Listener that will be called when the state is updated
     * @note Should be accompanied by a call to removeStateListener before listener goes out of scope to prevent leaks
     */
    public addStateListener(type: WindowStateType, listener: Function) {
        this.windowStateListeners.get(type)!.push(listener);
    }

    /**
     * Unregister a listener for a window state
     * @param type Type of window state to listen for
     * @param listener Listener that was registered in addStateListener
     */
    public removeStateListener(type: WindowStateType, listener: Function) {
        const listeners = this.windowStateListeners.get(type)!;
        const index = listeners.indexOf(listener);
        if (index !== -1) {
            listeners.splice(index, 1);
        }
    }

    private onFocus() {
        this.setWindowFocus(true);
    }

    private onBlur() {
        this.setWindowFocus(false);
    }

    private onFullscreenChange() {
        const doc: any = window.document;
        const fullscreen = !!(
            document.fullscreen ||
            doc.webkitIsFullScreen == true ||
            doc.mozFullScreen ||
            doc.msFullscreenElement
        );
        this.setFullscreen(fullscreen);
    }

    private onMouseEnter() {
        this.setMouseFocus(true);
    }

    private onMouseLeave() {
        this.setMouseFocus(false);
    }
}
