const enum WorkerTelemetryType {
    ERROR_EVENT = "ErrorEvent",
    ERROR_STRING = "ErrorString",
    MESSAGE_EVENT = "MessageEvent",
    UNUSED = "WorkerUnused",
    TIMEOUT = "WorkerTimeout",
    CREATE_FAILURE = "WorkerCreateFailure",
    OVERLONG_DELAY = "WorkerOverlongDelay"
}

